<template>
  <div class="single-post">
    <div class="post-container container">
      <!-- back button -->
      <div v-if="post">
        <router-link class="back-button" to="/">
          <span style="transform: translateY(-6px);">‚Üê</span>
        </router-link>

        <post-item :post="post" :is-post-page="true" />
      </div>
    </div>
  </div>
</template>

<script>
import PostItem from '@/components/PostItem'
import { computed } from 'vue'
import { useStore } from 'vuex'
import { onMounted, onUnmounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'

export default {
  components: {
    PostItem,
    // Head,
    // Comments,
  },
  setup() {
    const store = useStore()
    const route = useRoute()
    const router = useRouter()

    const post = computed(() => store.getters['post/item'])

    onMounted(async () => {
      try {
        await store.dispatch('post/loadPost', route.params.id)

        if (!post.value) {
          return router.push('/not-found')
        }

        if (post.value.messageId) {
          // await loadComments()
        }
      } catch (e) {
        console.log(e)
      }
    })

    onUnmounted(() => {
      store.commit('post/setPost', null)
    })

    return {
      post,
      // head,
    }
  },
}
</script>

